<template>
  <div class="middle">
    <transition  enter-active-class="animated bounceInLeft"
    leave-active-class="animated bounceOutRight"
       mode="out-in"        >
<router-view v-if="!$route.meta.keepAlive"></router-view>
    </transition>
    <div class="topic">
      <div class="title">  <img class="logo" src="../assets/logo.jpg" style="width:100px;" /></div>
    </div>
  </div>
</template>
<script>
 import animate from 'animate.css'
export default {
 name:"middle"
}

</script>
<style >
.middle{
    
    height: 100%;
    display: flex;
    justify-content:flex-start;
    padding: 0 0 0 5px;
    
}
.title{padding: 5px;}
.topic{
  width: 20%;
  height: 100%;
  background-color:rgba(255,255,255,.1);
  margin: 0   5px;
}
.middle-con{
    
    background-color:rgba(255,255,255,.1);
    width :80%;
}
 .logo{
  border: 1px solid transparent;
  border-radius: 10px;
}
</style>





