<template >
  <div class="head">
     <transition enter-active-class="animated hinge">
      <div v-show="showFlag" class="menu">
       <span><router-link to="home">Home</router-link></span>
       <span><router-link to="about">about</router-link></span>
       <span><router-link to="home">home</router-link></span>
       <span><router-link to="about">about</router-link></span>
      </div>     
     </transition>
  </div>
  
</template>
<script >
import animate from "animate.css";
export default {
  name: "head",
  data: function() {
    return {
      showFlag: false
    };
  },
  props: ["show"],
  watch: {
    show: function() {
      if (this.show == true) {
        setTimeout(() => {
          this.showFlag = true;
        }, 500);
      }
    }
  }
};
</script>

<style scoped>
.head {
  margin: 0 5px;
  padding: 5px 0;
  z-index: 999;
}
.head .menu {
  background-color: rgba(255,255,255,.1);
  display: flex;
  justify-content: space-around;

  color: #fff;
  padding: 5px 0;
}
.head .menu span {
  font-size: 28px;
  cursor: pointer;
}
.head .menu span a:hover {
  color: #000;
}
</style>




